.mainContainer {
    height: 94vh;
    background-color: white;
    margin: 3vh;
}

.headerContainer {
    height: 15vh;
    display: flex;
    align-items: center;
    justify-content: center;
    border: solid #E9E5E5;
    border-radius: 5vh;
    background-color: #F0EEEE;
}

.headerContainer p {
    color: white;
}

.headerContainer img {
    object-fit: contain;
    max-height: 30vh;
    max-width: 30vh;
}

.bodyContainer {
    display: flex;
    flex-direction: row;
    height: 75vh;
}

.menuContainer {
    flex: 1;
    border: solid #E9E5E5;
    border-radius: 5vh;
    background-color: #F0EEEE;
    margin-top: 2vh;
    margin-bottom: 2vh;
}

.menuContainerSignInContainer {
    background-color: #F2F1F1;
    flex-direction: column;
    display: flex;
    border: dotted #CBC6C6;
    margin: 2vh;
    padding: 1vh;
    border-radius: 1vh;
}

.menuContainerSignInContainer Button {
    margin: 0.4vh;
}

.menuContainerText {
    text-align: center;
    font-family: "Arial";
    font-weight: bold;
    text-decoration: underline;
}

.contentContainer {
    flex: 5;
    border: solid #E9E5E5;
    background-color: #F0EEEE;
    margin-top: 2vh;
    margin-bottom: 2vh;
    margin-left: 5vh;
    display: flex;
    flex-direction: column;
}

.contentContainerScrollable {
    background-color: white;
    overflow-y: scroll;
    max-height: 30vh;
}

.contentContainerResult {
    background-color: black;
    color: #61CFE1;
    font-family: Monaco;
    font-style: normal;
    flex: 1;
    word-break: break-all;
    overflow-y: scroll;
}

.contentContainerResultText {
    padding-left: 1vh;
}

.footerContainer {
    border: solid #E9E5E5;
    border-radius: 2vh;
    background-color: #F0EEEE;
    height: 4vh;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-family: Arial;
    text-decoration: underline;
}
